.frame-child89 {
  width: 174px;
  height: 240px;
  position: relative;
  border-radius: var(--br-8xs);
  background-color: var(--color-white);
  border: 1px solid var(--color-gainsboro-200);
  box-sizing: border-box;
  display: none;
}
.frame-web-development {
  width: 151px;
  height: 151px;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
  z-index: 1;
}
.isro1 {
  align-self: stretch;
  height: 30px;
  position: relative;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  z-index: 1;
}
.frame-child90 {
  height: 31px;
  width: 123px;
  position: relative;
  border-radius: var(--br-6xl);
  background-color: var(--color-white);
  border: 1px solid var(--color-blueviolet-200);
  box-sizing: border-box;
  display: none;
}
.follow3 {
  height: 30px;
  width: 101px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  z-index: 2;
}
.rectangle-parent64,
.rectangle-parent65 {
  background-color: var(--color-white);
  display: flex;
  align-items: center;
}
.rectangle-parent65 {
  width: 123px;
  border-radius: var(--br-6xl);
  border: 1px solid var(--color-blueviolet-200);
  box-sizing: border-box;
  flex-direction: row;
  justify-content: center;
  padding: var(--padding-12xs) 0 0;
  z-index: 1;
}
.rectangle-parent64 {
  border-radius: var(--br-8xs);
  border: 1px solid var(--color-gainsboro-200);
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-3xs) var(--padding-xs) var(--padding-xs)
    var(--padding-4xs);
  gap: var(--gap-10xs);
  text-align: center;
  font-size: var(--font-size-sm);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
