.frame-child24 {
  align-self: stretch;
  height: 558px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.25);
  border: 1px solid var(--color-blueviolet-200);
  box-sizing: border-box;
  display: none;
}
.registration-form1 {
  margin: 0;
  width: 231px;
  position: relative;
  font-size: inherit;
  font-weight: 600;
  font-family: inherit;
  display: inline-block;
  z-index: 1;
}
.enter-your-details {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-sm);
  z-index: 1;
}
.registration-form-parent,
.team-leader-name-frame {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.registration-form-parent {
  flex: 1;
  flex-direction: column;
}
.team-leader-name-frame {
  width: 397px;
  flex-direction: row;
  padding: 0 var(--padding-6xl);
  box-sizing: border-box;
}
.line-separator-icon {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: contain;
  margin-top: -1px;
  z-index: 1;
}
.team-leader-name-frame-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  max-width: 100%;
}
.frame-child25,
.frame-child26 {
  border: 0;
  background-color: transparent;
  align-self: stretch;
  height: 50px;
  font-family: var(--font-inter);
  font-size: var(--font-size-lg);
  color: var(--color-darkgray);
  z-index: 1;
}
.frame-child26 {
  height: 51px;
  z-index: 3;
}
.add-team-member-button-child {
  height: 40px;
  width: 192px;
  position: relative;
  border-radius: var(--br-6xl);
  background-color: var(--color-white);
  border: 1px solid var(--color-blueviolet-200);
  box-sizing: border-box;
  display: none;
}
.add-team-member {
  font-weight: 600;
}
.span {
  font-size: var(--font-size-lg);
  font-weight: 900;
}
.add-team-member-container {
  height: 30px;
  flex: 1;
  position: relative;
  display: flex;
  align-items: center;
  z-index: 2;
}
.add-team-member-button {
  flex: 1;
  border-radius: var(--br-6xl);
  background-color: var(--color-white);
  border: 1px solid var(--color-blueviolet-200);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-8xs) var(--padding-7xs) var(--padding-8xs)
    var(--padding-9xs);
  white-space: nowrap;
  z-index: 1;
}
.frame-parent9,
.team-leader-name-field {
  width: 204px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-7xs);
  box-sizing: border-box;
}
.frame-parent9 {
  width: 379px;
  flex-direction: column;
  padding: 0 var(--padding-12xs) var(--padding-mid) 0;
  gap: var(--gap-mini);
  max-width: 100%;
  text-align: center;
  font-size: var(--font-size-sm);
  font-family: var(--font-montserrat);
}
.frame-child27 {
  height: 48px;
  width: 169px;
  position: relative;
  border-radius: var(--br-16xl);
  background-color: var(--color-blueviolet-200);
  display: none;
}
.submit {
  align-self: stretch;
  flex: 1;
  position: relative;
  font-size: var(--font-size-6xl);
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  text-align: center;
  z-index: 2;
}
.frame-button {
  cursor: pointer;
  border: 0;
  padding: var(--padding-5xs) var(--padding-xs) var(--padding-xl)
    var(--padding-2xs);
  background-color: var(--color-blueviolet-200);
  height: 48px;
  flex: 1;
  border-radius: var(--br-16xl);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  box-sizing: border-box;
  z-index: 1;
}
.frame-button:hover {
  background-color: var(--color-blueviolet-100);
}
.compet-reg-form,
.frame-section,
.frame-wrapper3 {
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
}
.frame-wrapper3 {
  width: 191px;
  flex-direction: row;
  align-items: flex-start;
  padding: 0 0 0 var(--padding-3xl);
}
.compet-reg-form,
.frame-section {
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  max-width: 100%;
  text-align: left;
  font-family: var(--font-inter);
}
.frame-section {
  flex: 1;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.25);
  border: 1px solid var(--color-blueviolet-200);
  flex-direction: column;
  align-items: center;
  padding: var(--padding-sm) 0 var(--padding-99xl);
  gap: var(--gap-44xl);
  flex-shrink: 0;
  font-size: var(--font-size-6xl);
  color: var(--color-black);
}
.compet-reg-form {
  width: 963px;
  overflow: hidden;
  flex-direction: row;
  align-items: flex-end;
  padding: var(--padding-55xl) var(--padding-160xl) var(--padding-37xl)
    var(--padding-128xl);
  letter-spacing: normal;
  max-height: 100%;
  font-size: var(--font-size-lg);
  color: var(--color-darkgray);
}
@media screen and (max-width: 650px) {
  .frame-section {
    gap: var(--gap-44xl);
  }
}
@media screen and (max-width: 625px) {
  .frame-section {
    padding-top: var(--padding-xl);
    padding-bottom: var(--padding-58xl);
    box-sizing: border-box;
  }
  .compet-reg-form {
    padding-left: var(--padding-54xl);
    padding-right: var(--padding-70xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .registration-form1,
  .submit {
    font-size: var(--font-size-xl);
  }
  .frame-section {
    gap: var(--gap-44xl);
  }
}
