.desktop-7-child {
  width: 260px;
  height: 732px;
  position: relative;
  background-color: var(--color-white);
  display: none;
}
.rectangle-frame-icon {
  width: 95px;
  height: 30px;
  position: relative;
  border-radius: var(--br-3xs);
  object-fit: cover;
}
.privacy-policy4 {
  position: relative;
  font-weight: 500;
}
.legal-notices {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.privacy-policy-terms-report1 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl) 0 242px;
}
.desktop-7 {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-91xl);
  box-sizing: border-box;
  gap: var(--gap-801xl);
  letter-spacing: normal;
  text-align: center;
  font-size: var(--font-size-sm);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 750px) {
  .privacy-policy-terms-report1 {
    padding-left: 121px;
    box-sizing: border-box;
  }
  .desktop-7 {
    gap: var(--gap-801xl);
  }
}
@media screen and (max-width: 450px) {
  .privacy-policy-terms-report1 {
    padding-left: var(--padding-xl);
    box-sizing: border-box;
  }
  .desktop-7 {
    gap: var(--gap-801xl);
  }
}
