.frame-child85 {
  align-self: stretch;
  width: 400px;
  position: relative;
  border-radius: var(--br-8xs);
  background-color: var(--color-royalblue);
  display: none;
  max-width: 100%;
}
.competitons {
  position: absolute;
  right: -69px;
  bottom: -61.5px;
  font-size: var(--font-size-6xl);
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  text-align: left;
  display: inline-block;
  width: 218px;
  height: 116px;
}
.rectangle-header {
  height: 75px;
  flex: 1;
  position: relative;
  border-radius: var(--br-8xs);
  min-width: 97px;
  z-index: 1;
}
.community-icon,
.rectangle-parent61 {
  flex: 1;
  border-radius: var(--br-8xs);
  max-width: 100%;
}
.community-icon {
  height: 104px;
  position: relative;
  overflow: hidden;
  object-fit: cover;
  min-width: 101px;
  z-index: 1;
}
.rectangle-parent61 {
  cursor: pointer;
  border: 0;
  padding: 0 0 0 var(--padding-20xl);
  background-color: var(--color-royalblue);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  box-sizing: border-box;
  gap: var(--gap-38xl);
  min-width: 260px;
}
@media screen and (max-width: 450px) {
  .competitons {
    font-size: var(--font-size-xl);
  }
  .rectangle-parent61 {
    flex-wrap: wrap;
    gap: var(--gap-38xl);
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
  }
}
