.opportunity-page-child {
  width: 260px;
  height: 732px;
  position: relative;
  background-color: var(--color-white);
  display: none;
}
.content-creator,
.fiddle-logo {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.fiddle-logo {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-12xs);
}
.content-creator {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-22xl) var(--padding-45xl);
  box-sizing: border-box;
}
.rectangle-container-icon {
  width: 95px;
  height: 30px;
  position: relative;
  border-radius: var(--br-3xs);
  object-fit: cover;
}
.privacy-policy1 {
  margin: 0;
  position: relative;
  font-size: inherit;
  font-weight: 500;
  font-family: inherit;
}
.root-frame1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.rectangle-privacy-policy-frame {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl) 0 236px;
}
.opportunity-page {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-29xl);
  box-sizing: border-box;
  gap: var(--gap-7xs);
  letter-spacing: normal;
  text-align: center;
  font-size: var(--font-size-sm);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 975px) {
  .content-creator {
    padding-bottom: 42px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 700px) {
  .rectangle-privacy-policy-frame {
    padding-left: var(--padding-99xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .content-creator {
    padding-bottom: var(--padding-8xl);
    box-sizing: border-box;
  }
  .rectangle-privacy-policy-frame {
    padding-left: var(--padding-xl);
    box-sizing: border-box;
  }
}
