.organizatiom-child {
  width: 260px;
  height: 732px;
  position: relative;
  background-color: var(--color-white);
  display: none;
}
.featured-season-frame,
.gifts-rs-lakh-frame {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.gifts-rs-lakh-frame {
  width: 1379px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 var(--padding-10xs) 0 var(--padding-xl);
}
.featured-season-frame {
  align-self: stretch;
  flex-direction: row;
  justify-content: flex-end;
  padding: 0 0 330px;
}
.rectangle-frame-icon2 {
  width: 95px;
  height: 30px;
  position: relative;
  border-radius: var(--br-3xs);
  object-fit: cover;
}
.privacy-policy5 {
  position: relative;
  font-weight: 500;
}
.legal-notices1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 0 0 0 var(--padding-xl);
  gap: var(--gap-5xs);
}
.fiddle-copyright {
  width: 524px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  max-width: 100%;
}
.organizatiom {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-21xl) var(--padding-45xl) 0;
  box-sizing: border-box;
  gap: var(--gap-7xs);
  letter-spacing: normal;
  text-align: center;
  font-size: var(--font-size-sm);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1200px) {
  .featured-season-frame {
    padding-bottom: 214px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .featured-season-frame {
    padding-bottom: 139px;
    box-sizing: border-box;
  }
}
