.frame-group-you,
.frame-group-you-wrapper,
.label-node {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.label-node {
  width: 167px;
  flex: 1;
  flex-direction: row;
  gap: var(--gap-xs);
}
.frame-group-you,
.frame-group-you-wrapper {
  align-self: stretch;
}
.frame-group-you {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-lgi);
}
.frame-group-you-wrapper {
  height: 124px;
  flex-direction: row;
  padding: 0 var(--padding-7xs) 0 var(--padding-2xs);
  box-sizing: border-box;
}
.image-19-icon4 {
  height: 31.6px;
  width: 30px;
  position: relative;
  object-fit: cover;
  z-index: 1;
}
.your-interest4 {
  height: 28px;
  flex: 1;
  position: relative;
  letter-spacing: -0.05em;
  font-weight: 600;
  display: flex;
  align-items: center;
  z-index: 1;
}
.rectangle-shape,
.rounded-rectangle {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.rounded-rectangle {
  flex: 1;
  gap: var(--gap-3xs);
}
.rectangle-shape {
  width: 185px;
  padding: 0 var(--padding-5xs);
  box-sizing: border-box;
}
.sector-chandigarh-frame,
.world-cup-screening-frame2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.sector-chandigarh-frame {
  align-self: stretch;
  gap: var(--gap-mini);
}
.world-cup-screening-frame2 {
  width: 209px;
  padding: 0 0 var(--padding-6xs);
  box-sizing: border-box;
}
.frame-group-links {
  height: 398px;
  flex: 1;
  position: relative;
  border-radius: var(--br-8xs);
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  min-width: 711px;
}
.fiddle-copyright-frame {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-30xl);
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1050px) {
  .frame-group-links {
    min-width: 100%;
  }
}
@media screen and (max-width: 750px) {
  .fiddle-copyright-frame {
    gap: var(--gap-30xl);
  }
}
