.add-a-heading-4-12 {
  width: 363px;
  position: relative;
  max-height: 100%;
  object-fit: cover;
  max-width: 100%;
  flex-shrink: 0;
  cursor: pointer;
}
.sign-in1 {
  color: #0248ff;
}
.already-a-user-container {
  margin: 0;
  width: 394px;
  height: 37px;
  position: relative;
  font-size: inherit;
  font-weight: 600;
  font-family: inherit;
  display: inline-block;
  flex-shrink: 0;
  max-width: 100%;
  padding-left: var(--padding-xl);
  padding-right: var(--padding-xl);
  cursor: pointer;
}
.frame-parent3 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 30px;
  max-width: 100%;
}
.desktop-2,
.desktop-2-inner {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}
.desktop-2-inner {
  width: 634px;
  flex-direction: column;
  padding: 100px 0 0;
  min-width: 634px;
  max-width: 100%;
  flex-shrink: 0;
}
.desktop-2 {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: row;
  padding: var(--padding-3xl) 0 var(--padding-31xl);
  gap: var(--gap-35xl);
  letter-spacing: normal;
  text-align: center;
  font-size: var(--font-size-6xl);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 1000px) {
  .add-a-heading-4-12,
  .desktop-2-inner {
    flex: 1;
  }
  .desktop-2 {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 975px) {
  .desktop-2-inner {
    padding-top: 65px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 725px) {
  .desktop-2-inner {
    min-width: 100%;
  }
  .desktop-2 {
    gap: var(--gap-35xl);
  }
}
@media screen and (max-width: 450px) {
  .already-a-user-container {
    font-size: var(--font-size-xl);
  }
}
