.enterusername-frame,
.frame-parent23 {
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}
.frame-parent23 {
  margin: 0;
  flex: 1;
  flex-direction: column;
  align-items: center;
  gap: var(--gap-mini);
}
.enterusername-frame {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  padding: 0 0 var(--padding-xs) var(--padding-10xs);
  box-sizing: border-box;
}
.continue-button1 {
  width: 153px;
  height: 1px;
  position: relative;
  border-top: 1px solid var(--color-black);
