  margin-left: -150px;
}
.line-frame-child {
  align-self: stretch;
  height: 1px;
  position: relative;
  border-top: 1px solid var(--color-black);
  box-sizing: border-box;
  z-index: 1;
}
.frame-parent24,
.line-frame {
  display: flex;
  justify-content: flex-start;
}
.line-frame {
  height: 6px;
  width: 152px;
  flex-direction: column;
  align-items: flex-start;
