  font-weight: 600;
  display: inline-block;
  z-index: 2;
}
.rectangle-vector-child {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-11xl);
  width: 100%;
  height: 100%;
  z-index: 1;
}
.image-1-icon1 {
  position: absolute;
  top: 12px;
  left: 232px;
  width: 75px;
  height: 36px;
  object-fit: cover;
  z-index: 2;
}
.rectangle-vector {
  height: 51px;
  width: 378px;
  position: absolute;
  margin: 0 !important;
  top: -15px;
  right: -68px;
}
.continue-button-parent,
.sign-in-parent {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.continue-button-parent {
  width: 314px;
  align-items: flex-start;
