.get-event-dashboard-child {
  width: 361px;
  height: 157px;
  position: relative;
  border-radius: var(--br-3xs);
  background: linear-gradient(-58.09deg, #8142ff, rgba(235, 225, 255, 0));
  border: 1px solid var(--color-silver);
  box-sizing: border-box;
  display: none;
  max-width: 100%;
}
.manage {
  margin: 0;
}
.colloege-fests {
  margin: 0;
  font-size: var(--font-size-xs);
  font-weight: 500;
}
.manage-colloege-fests-container {
  align-self: stretch;
  position: relative;
  z-index: 1;
}
.get-an-event {
  height: 78px;
  flex: 1;
  position: relative;
  font-weight: 500;
  display: flex;
  align-items: center;
  z-index: 1;
}
.copyright-assistance,
.get-event-dashboard {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-2xs) 0 0;
  font-size: var(--font-size-base);
}
.get-event-dashboard {
  border-radius: var(--br-3xs);
  background: linear-gradient(-58.09deg, #8142ff, rgba(235, 225, 255, 0));
  border: 1px solid var(--color-silver);
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  padding: var(--padding-mid) var(--padding-3xs) var(--padding-lgi)
    var(--padding-lg);
  gap: var(--gap-7xs);
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-6xl);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
