.ellipse-image-originalcreation {
  width: 201px;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  border: 1px solid var(--color-silver);
  box-sizing: border-box;
  z-index: 1;
}
.span1 {
  font-size: var(--font-size-6xl);
}
.become,
.creator {
  align-self: stretch;
  height: 43px;
  position: relative;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  z-index: 2;
}
.creator {
  font-size: var(--font-size-6xl);
  font-weight: 400;
  font-family: inherit;
  z-index: 3;
  margin: -20px 0 0;
}
.become-parent {
  width: 269px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame-child72 {
  height: 26px;
  width: 180px;
  position: relative;
  border-radius: var(--br-6xl);
  background-color: var(--color-mediumslateblue);
  display: none;
}
.opportunity-creator1 {
  flex: 1;
  position: relative;
  letter-spacing: -0.1em;
  font-weight: 600;
  z-index: 1;
}
.frame-child73,
.rectangle-parent48 {
  border-radius: var(--br-6xl);
  background-color: var(--color-mediumslateblue);
}
.rectangle-parent48 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-10xs) var(--padding-5xs) var(--padding-10xs) 0;
  white-space: nowrap;
  cursor: pointer;
  z-index: 4;
}
.frame-child73 {
  height: 26px;
  width: 180px;
  position: relative;
  display: none;
}
.content-creator1 {
  flex: 1;
  position: relative;
  letter-spacing: -0.1em;
  font-weight: 600;
  z-index: 1;
}
.rectangle-parent49 {
  align-self: stretch;
  border-radius: var(--br-6xl);
  background-color: var(--color-mediumslateblue);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-10xs) var(--padding-5xs) var(--padding-10xs) 0;
  white-space: nowrap;
  z-index: 2;
}
.frame-web-development-project1,
.line-frame-separator {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.line-frame-separator {
  width: 181px;
  flex-direction: column;
  padding: 0 var(--padding-12xs) 0 0;
  box-sizing: border-box;
  gap: var(--gap-6xs);
}
.frame-web-development-project1 {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-2xs);
  margin-top: -2px;
