  font-size: var(--font-size-base);
  color: var(--color-white);
}
.frame-web-development-projects {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  padding: var(--padding-3xs) 0 var(--padding-12xl);
  position: relative;
}
.span2 {
  font-weight: 600;
}
.fiddle-20231 {
  font-weight: 300;
}
.fiddle-2023 {
  height: 24px;
  flex: 1;
