.compet-reg-form-item {
  height: 51px;
  width: 378px;
  position: relative;
  border-radius: var(--br-11xl);
  background-color: var(--color-white);
  border: 1px solid var(--color-blueviolet-200);
  box-sizing: border-box;
  display: none;
  max-width: 100%;
}
.team-leader-name1 {
  height: 26px;
  width: 207px;
  position: relative;
  display: none;
  flex-shrink: 0;
}
.frame-child28 {
  align-self: stretch;
  height: 558px;
  position: relative;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.25);
  border: 1px solid var(--color-blueviolet-200);
  box-sizing: border-box;
  display: none;
}
.become-creator,
.enter-your-details1 {
  position: relative;
  font-family: var(--font-inter);
  color: var(--color-black);
  text-align: left;
  z-index: 1;
}
.become-creator {
  margin: 0;
  width: 231px;
  height: 31px;
  font-size: var(--font-size-6xl);
  font-weight: 600;
  display: inline-block;
  flex-shrink: 0;
}
.enter-your-details1 {
  align-self: stretch;
  font-size: var(--font-size-sm);
}
.become-creator-parent,
.domain-frame {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.become-creator-parent {
  flex: 1;
  flex-direction: column;
}
.domain-frame {
  width: 397px;
  flex-direction: row;
  padding: 0 var(--padding-6xl);
  box-sizing: border-box;
}
.line-separator-icon1 {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: contain;
  margin-top: -1px;
  z-index: 1;
}
.domain-frame-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  max-width: 100%;
}
.frame-child29,
.frame-child30,
.parent {
  align-self: stretch;
  font-family: var(--font-inter);
  font-size: var(--font-size-lg);
  color: var(--color-darkgray);
}
.frame-child29 {
  border: 0;
  background-color: transparent;
  height: 50px;
  z-index: 1;
}
.frame-child30,
.parent {
  height: 51px;
  z-index: 3;
}
.frame-child30 {
  border: 0;
  background-color: transparent;
  height: 104px;
  z-index: 1;
}
.frame-parent10 {
  width: 379px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-12xs) 0 0;
  box-sizing: border-box;
  gap: var(--gap-base);
  max-width: 100%;
}
.frame-child31 {
  height: 48px;
  width: 169px;
  position: relative;
  border-radius: var(--br-16xl);
  background-color: var(--color-blueviolet-200);
  display: none;
}
.submit1 {
  align-self: stretch;
  flex: 1;
  position: relative;
  font-size: var(--font-size-6xl);
  font-weight: 600;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  text-align: center;
  z-index: 1;
}
.rectangle-parent18 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-5xs) var(--padding-xs) var(--padding-xl)
    var(--padding-2xs);
  background-color: var(--color-blueviolet-200);
  width: 169px;
  height: 48px;
  border-radius: var(--br-16xl);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  box-sizing: border-box;
  z-index: 1;
}
.rectangle-parent18:hover {
  background-color: var(--color-blueviolet-100);
}
.compet-reg-form1,
.frame-form {
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.frame-form {
  margin: 0;
  flex: 1;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.25);
  border: 1px solid var(--color-blueviolet-200);
  flex-direction: column;
  align-items: center;
  padding: var(--padding-sm) 0 68px;
  gap: var(--gap-45xl);
  flex-shrink: 0;
}
.compet-reg-form1 {
  width: 963px;
  overflow: hidden;
  flex-direction: row;
  align-items: flex-end;
  padding: var(--padding-55xl) var(--padding-160xl) var(--padding-37xl)
    var(--padding-128xl);
  letter-spacing: normal;
  max-height: 100%;
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-darkgray);
  font-family: var(--font-inter);
}
@media screen and (max-width: 650px) {
  .frame-form {
    gap: var(--gap-45xl);
  }
}
@media screen and (max-width: 625px) {
  .frame-form {
    padding-top: var(--padding-xl);
    padding-bottom: 44px;
    box-sizing: border-box;
  }
  .compet-reg-form1 {
    padding-left: var(--padding-54xl);
    padding-right: var(--padding-70xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .become-creator,
  .submit1 {
    font-size: var(--font-size-xl);
  }
  .frame-form {
    gap: var(--gap-45xl);
  }
}
